@import url(https://fonts.googleapis.com/css?family=Raleway:200,300,400,500,600,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald:200,300,400,500,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root {
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #222222;
}

.app-container {
  height: 100%;
  align-items: center;
  font-family: "Raleway", sans-serif;
  color: white;
}

.app-container > div {
  position: absolute;
  width: 100%;
  height: 100%;
  will-change: transform, opacity;
}

/* ============================================================================ */
/* OVERLAY */
.overlay-container {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: #222222;
  z-index: 10;
  font-family: "Oswald", sans-serif;
}
.overlay-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  align-items: center;
  height: 77%;
}
.overlay-title {
  color: #bdff44;
  font-size: 95px;
  /* text-align: center; */
  line-height: 93px;
  padding: 0 20px;
}

/* ============================================================================ */
/* BIG BUTTON */
.big-button {
  position: fixed;
  top: 87vh;
  right: 25px;
  z-index: 99;
}

.input-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  /*  width: 100%; */
  box-sizing: border-box;
}

.input-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.input {
  height: 45px;
  margin-top: 15px;
  /* width: 369px; */
  border: 2px #7ee500 solid;
  border-radius: 5px;
  padding: 10px;
  font-size: 15px;
}
.smaller-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.smaller-container > input {
  width: 45%;
}


.form-container {
  width: 100vw;
  height: 100vh;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-container > .form-section {
  box-sizing: border-box;
  padding: 0 40px;
  width: 40%;
}

.form-header > h1 {
  color: white;
  font-weight: 800;
  padding-left: 16px;
}

.form-footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.form-footer > .form-add-btn {
  border: none;
  background-color: #bdff44;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 10px;
  color: #070217;
}

.header-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  border-bottom: 2px #bdff44 solid;
  margin-bottom: 20px;
  padding-left: 16px;
  width: 100%;
  color: white;
}

.exercise-container {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 10px;
  background-color: #1d192c;
  margin: 20px 0;
  box-sizing: border-box;
}

.exercise-list {
  list-style-type: none;
  margin-left: 35px;
}

.card-container {
  width: 350px;
  height: auto;
  background-color: #070217;
  color: white;
  margin: 20px;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 22px;
}

.card-header {
  margin: 20px 0;
  display: flex;
  flex-direction: row;

  border-bottom: 2px #7ee500 solid;
}
.title {
  font-size: 22px;
  font-weight: 600;
  margin-top: 0;
}

.day-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
