.card-container {
  width: 350px;
  height: auto;
  background-color: #070217;
  color: white;
  margin: 20px;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 22px;
}

.card-header {
  margin: 20px 0;
  display: flex;
  flex-direction: row;

  border-bottom: 2px #7ee500 solid;
}
.title {
  font-size: 22px;
  font-weight: 600;
  margin-top: 0;
}
